<template>
  <div class="yeardetial">
    <sHeader :name= "title"  />
    <van-collapse v-for="(item, index) in yearList" :key="index" v-model="item.active" accordion>
      <van-collapse-item  :title="item['date']" :name="1" >
          <div class="list_record"  >
            <div> <span>早餐摄入</span> <span>{{ item['breakfast'].sumValue}}千卡</span> </div>
            <div> <span>午餐摄入</span> <span>{{ item['lunch'].sumValue}}千卡</span> </div>
            <div> <span>晚餐摄入</span> <span>{{ item['dinner'].sumValue}}千卡</span> </div>
            <div> <span>加餐摄入</span> <span>{{ item['add'].sumValue}}千卡</span> </div>
          </div>
      </van-collapse-item>
    </van-collapse>
  </div>
</template>

<script>
import {onMounted, reactive, toRefs} from "vue";
import sHeader from '@/components/SimpleHeader'
import { useRoute } from "vue-router";

export default {
  name: "YearDetialList",
  components:{
    sHeader
  },
  setup(){
    const router = useRoute()

    /**
     * 声明
     * @type {UnwrapNestedRefs<{list: []}>} 数据列表
     */
    const state = reactive({
      yearList:[],
      params: {},
      title:""
    })

    onMounted(()=>{
      const { query: { itemJson } } = router
      const item = JSON.parse(itemJson)
      state.title = item.yearDate
      state.yearList = item.origin
      console.log('进入年列表界面',state.yearList)
    })

    const initDate = () =>{
      const mlist = []
      for (let i = 0; i < 5; i++) {
        mlist.push({
          date: `2021-8-${Number(13+i)}`,
          zaoCan: 100,
          wuCan: 400,
          wanCan: 500,
          jiaCan: i+100,
          sum: 1000,
          value: 120+ i*2
        })
      }
      return mlist
    }
    return {
      ...toRefs(state)
    }
  }
}
</script>

